<template>
  <div id="app">
    <div>
      <vue-good-table
        title="Job automation probability"
        :columns="columns"
        :rows="rows"
        :paginate="true"
        :lineNumbers="false"/>
    </div>
  </div>
</template>

<script>
import rankData from '../data/probabilityranks.json'

export default {
  name: 'test',
  data() {
    return {
        columns: [
          {
            label: 'Rank',
            field: 'Rank',
          },
          {
            label: 'Probability',
            field: 'Probability',
            type: 'percentage',
            html: false,
          },
          {
            label: 'SOC code',
            field: 'SOC_code',
            type: 'number',
          },
          {
            label: 'Occupation',
            field: 'Occupation',
            html: false,
            filterable: true
          },
        ],
        rows: rankData
        };
  }
}

</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



